                    },
                ],
                figures: [],
                passThrough: false,
                speedup: false,
                speed: 0.05,
                score: 0,
                highScore: 0,
                winScore: 4,
                showPartners: false,
                instructions: false,
                step: 1,
                start: false,
                gameon: false,
                win: false,
                board: [],
                boardClass: [],
                preview: [],
                brickSize: 4,
                smartRandom: true,
                emptyColor: '',
                img: {
                    puzzleBg, Logo,
                    vokzalfin, olimpfin, tsumfin, bibliofin, parusfin, facebookImg, twitterImg
                },
                nameObject: [
                    'Вокзал «Південний» в Києві',
                    'НСК Олімпійський',
                    'ЦУМ',
                    'Національнy бібліотекy України ім. Вернадського',
                    'БЦ Парус'
                ],
                finScore: [
                    3, 4, 6, 11, 12
                ]
            }
        },
        components: {
            TetrisBoard, PerfectScrollbar
        },
        watch: {
            start: function (val) {
                if (val) {
                    this.newGame();
                    let el = this.$refs.boardControl;
                    el.focus();
                    // check it twice...
                    setTimeout(() => el.focus(), 500);
                }
            }
        },
        methods: {
            randomInteger(min, max) {
                // получить случайное число от (min-0.5) до (max+0.5)
                let rand = min - 0.5 + Math.random() * (max - min + 1);
                return Math.round(rand);
            },
            nextLevel() {
                this.step += 1;
                this.win = false;
                this.start = true;
                this.$nextTick(function () {
                    this.initFigures();
                    this.newGame();
                    this.$refs.board.style.backgroundPositionY = 0;
                    let el = this.$refs.boardControl;
                    el.focus();
                    // check it twice...
                    setTimeout(() => el.focus(), 500);
                })


            },
            toggleFigures(e) {
                for (let f of this.figuresAll)
                    f.disabled = e.target.checked;
            },

            pause() {
                switch (this.state) {
                    case 'pause':
                        this.state = 'running';
                        this.calcSpeed();
                        break;
                    case 'running':
                        this.state = 'pause';
                        this.calcSpeed();
                        break;
                }
            },

            initBoard() {
                this.board.length = this.board.w * this.board.h;
                for (let i = 0; i < this.board.length; ++i) {
                    this.board[i] = this.emptyColor;
                }
                if (this.step === 1) {
                    for (let i = 1; i < 11; i++) {
                        this.board[(i - 1) + (this.board.h - 1) * this.board.w] = ' prev';
                    }
                    for (let i = 2; i < 11; i++) {
                        this.board[(i - 1) + (this.board.h - 2) * this.board.w] = ' prev';
                    }
                    for (let i = 2; i < 11; i++) {
                        this.board[(i - 1) + (this.board.h - 3) * this.board.w] = ' prev';
                    }
                    for (let i = 6; i < 8; i++) {
                        this.board[(i - 1) + (this.board.h - 4) * this.board.w] = ' prev';
                    }
                } else if (this.step === 2) {
                    for (let i = 1; i < 11; i++) {
                        this.board[(i - 1) + (this.board.h - 1) * this.board.w] = ' prev';
                    }
                    for (let i = 2; i < 10; i++) {
                        for (let j = 2; j < 5; j++)
                            this.board[(i - 1) + (this.board.h - j) * this.board.w] = ' prev';
                    }
                    for (let i = 4; i < 8; i++) {
                        this.board[(i - 1) + (this.board.h - 5) * this.board.w] = ' prev';
                    }
                } else if (this.step === 3) {
                    for (let i = 1; i < 11; i++) {
                        this.board[(i - 1) + (this.board.h - 1) * this.board.w] = ' prev';
                    }
                    for (let i = 2; i < 11; i++) {
                        for (let j = 2; j < 5; j++)
                            this.board[(i - 1) + (this.board.h - j) * this.board.w] = ' prev';
                    }
                    for (let i = 3; i < 9; i++) {
                        this.board[(i - 1) + (this.board.h - 5) * this.board.w] = ' prev';
                    }
                    for (let i = 4; i < 8; i++) {
                        this.board[(i - 1) + (this.board.h - 6) * this.board.w] = ' prev';
                    }
                    for (let i = 5; i < 8; i++) {
                        this.board[(i - 1) + (this.board.h - 7) * this.board.w] = ' prev';
                    }
                } else if (this.step === 5) {
                    for (let i = 3; i < 9; i++) {
                        this.board[(i - 1) + (this.board.h - 1) * this.board.w] = ' prev';
                    }
                    for (let i = 3; i < 9; i++) {
                        this.board[(i - 1) + (this.board.h - 2) * this.board.w] = ' prev';
                    }
                    for (let i = 3; i < 9; i++) {
                        this.board[(i - 1) + (this.board.h - 3) * this.board.w] = ' prev';
                    }
                    for (let i = 4; i < 9; i++) {
                        this.board[(i - 1) + (this.board.h - 4) * this.board.w] = ' prev';
                    }
                    for (let i = 4; i < 8; i++) {
                        for (let j = 5; j < 12; j++)
                            this.board[(i - 1) + (this.board.h - j) * this.board.w] = ' prev';
                    }
                    for (let i = 5; i < 8; i++) {
                        this.board[(i - 1) + (this.board.h - 12) * this.board.w] = ' prev';
                    }
                } else if (this.step === 4) {
                    for (let i = 1; i < 11; i++) {
                        for (let j = 1; j < 4; j++)
                            this.board[(i - 1) + (this.board.h - j) * this.board.w] = ' prev';
                    }
                    for (let i = 2; i < 8; i++) {
                        this.board[(i - 1) + (this.board.h - 4) * this.board.w] = ' prev';
                    }
                    for (let i = 3; i < 8; i++) {
                        for (let j = 5; j < 12; j++)
                            this.board[(i - 1) + (this.board.h - j) * this.board.w] = ' prev';
                    }
                    for (let i = 3; i < 5; i++) {
                        this.board[(i - 1) + (this.board.h - 12) * this.board.w] = ' prev';
                    }
                }


                //this.testBallBoard();
            },

            testBallBoard() {
                for (let y = this.board.h - 1; y > 5; y--) {
                    for (let x = 0; x < this.board.w; ++x)
                        if (x !== 5)
                            this.board[x + y * this.board.w] = '#080';
                }
                this.board[4 + 5 * this.board.w] = '#080';
                this.board[5 + 5 * this.board.w] = '#080';
                this.board[6 + 5 * this.board.w] = '#080';
                this.board[7 + 5 * this.board.w] = '#080';
            },

            initPreview() {
                this.preview.length = this.preview.w * this.preview.h;
                for (let i = 0; i < this.preview.length; ++i) {
                    this.preview[i] = this.emptyColor;
                }
            },

            initFigures() {
                for (let figure of this.figuresAll) {
                    let [x, y] = [0, 0];
                    if (!figure.anchor) {
                        for (let t of figure.points) {
                            x = Math.max(x, t[0]);
                            y = Math.max(y, t[1]);
                        }
                        figure.anchor = [x / 2, y / 2];
                    }
                    switch (figure.mode) {
                        case 'ball':
                        case 'explode':
                        case 'frozen':
                            figure.pointsRotated = [figure.points, figure.points, figure.points, figure.points];
                            break;
                        case 'rot':
                            figure.pointsRotated = [];
                            figure.pointsRotated.push(figure.points.map(p => [
                                Math.round(figure.anchor[0] + (p[0] - figure.anchor[0])),
                                Math.round(figure.anchor[1] + (p[1] - figure.anchor[1])),
                            ]));
                            figure.pointsRotated.push(figure.points.map(p => [
                                Math.round(figure.anchor[0] - (p[1] - figure.anchor[1])),
                                Math.round(figure.anchor[1] + (p[0] - figure.anchor[0])),
                            ]));
                            figure.pointsRotated.push(figure.points.map(p => [
                                Math.round(figure.anchor[0] - (p[0] - figure.anchor[0])),
                                Math.round(figure.anchor[1] - (p[1] - figure.anchor[1])),
                            ]));
                            figure.pointsRotated.push(figure.points.map(p => [
                                Math.round(figure.anchor[0] + (p[1] - figure.anchor[1])),
                                Math.round(figure.anchor[1] - (p[0] - figure.anchor[0])),
                            ]));
                            break;
                        case '90':
                            figure.pointsRotated = [];
                            figure.pointsRotated.push(figure.points.map(p => [
                                figure.anchor[0] + (p[0] - figure.anchor[0]),
                                figure.anchor[1] + (p[1] - figure.anchor[1]),
                            ]));
                            figure.pointsRotated.push(figure.points.map(p => [
                                figure.anchor[1] - (p[1] - figure.anchor[1]),
                                figure.anchor[0] + (p[0] - figure.anchor[0]),
                            ]));
                            break;
                        default:
                            throw 'unknown figure rotation mode: ' + figure.mode;
                    }
                }
            },

            setFigureToMap(map, figure, preview = false) {
                console.log(figure)
                let [x, y] = preview
                    ? [
                        -figure.figure.anchor[0] + map.w / 2 | 0,
                        -figure.figure.anchor[1] + map.h / 2 | 0
                    ]
                    : [figure.pos[0], figure.pos[1]];
                console.log(figure.figure.pointsRotated[figure.rotate])
                let j = 0;
                for (let p of figure.figure.pointsRotated[figure.rotate]) {
                    let px = x + p[0];
                    let py = y + p[1];
                    if (px >= 0 && px < map.w && py >= 0 && py < map.h) {
                        if (!map[px + py * map.w].match('background')) {
                            map[px + py * map.w] = figure.color + ' background' + figure.figure.bg[j];
                        } else {
                            map[px + py * map.w] = figure.color + ' background' + figure.figure.bg[j];
                        }
                        j++;
                        // classmap[px + py * map.w] = 'active';
                        // map[px + py * map.w] = figure.color;


                    }
                }
            },

            unsetFigureFromMap(map, figure, preview = false) {
                let [x, y] = preview
                    ? [
                        -figure.figure.anchor[0] + map.w / 2 | 0,
                        -figure.figure.anchor[1] + map.h / 2 | 0
                    ]
                    : [figure.pos[0], figure.pos[1]];

                [figure.pos[0], figure.pos[1]];
                for (let p of figure.figure.pointsRotated[figure.rotate]) {
                    let px = x + p[0];
                    let py = y + p[1];
                    if (px >= 0 && px < map.w && py >= 0 && py < map.h) {
                        map[px + py * map.w] = this.emptyColor;
                    }
                }
            },

            swapNew() {
                this.passThrough = false;
                this.current = this.next;
                if (this.checkCollide(this.board, this.current) || this.score >= this.finScore[this.step - 1])
                    return this.end();
                this.setFigureToMap(this.board, this.current);
                this.$refs.board.update();

                this.unsetFigureFromMap(this.preview, this.next, true);
                this.next = this.genNew()
                this.setFigureToMap(this.preview, this.next, true);
                this.$refs.preview.update();
            },

            genNew() {
                if (this.smartRandom) {
                    return this.genNewSmart();
                } else {
                    return this.genNewOld();
                }
            },

            genNewSmart() {
                let {bucket} = this;

                let fill = false;

                while (bucket[0] !== this.last) {
                    if (!bucket.length) {
                        this.fillBucket();
                        fill = true;
                    }

                    for (let i = 0; i < bucket.length - 1; ++i) {
                        if (bucket[0] !== this.last) break;
                        bucket.push(bucket.shift());
                    }

                    if (bucket[0] === this.last && !fill) {
                        bucket.length = 0;
                        continue;
                    }

                    break;
                }

                let figure = bucket.shift();

                let current = {
                    figure,
                    rotate: 0,
                };

                this.last = figure;

                current.color = figure.color ||
                    `hsl(${360 * Math.random() | 0}, 100%, 50%)`;
                current.image = figure.image;

                current.pos = [
                    this.board.w / 2 - figure.anchor[0] | 0,
                    0,
                ];

                return current;
            },

            fillBucket() {
                let {bucket, figures} = this;
                bucket.length = 0;

                /*this.bucket.push(this.figures[
                    Math.random()*this.figures.length|0
                ]);*/

                bucket.push(...figures);
                // make two sets in short
                bucket.push(...figures);

                for (let i = 0; i < bucket.length - 1; ++i) {
                    let available = bucket.length - i - 1;
                    let randPick = Math.random() * available | 0;
                    let j = i + randPick;
                    [bucket[i], bucket[j]] = [bucket[j], bucket[i]];
                }
            },

            genNewOld() {
                let current = {};
                current.figure = this.figures[
                Math.random() * this.figures.length | 0
                    ];
                current.rotate = 0;
                current.color = current.figure.color ||
                    `hsl(${360 * Math.random() | 0}, 100%, 50%)`;

                current.image = current.figure.image;
                current.pos = [this.board.w / 2 - current.figure.anchor[0] | 0, 0];
                return current;
            },
            moveDown() {
                this.speedup = true;
                this.calcSpeed();
                this.loop();
            },
            keydown(e) {
                switch (e.which) {
                    case 'N'.charCodeAt(0):
                        this.newGame();
                        break;
                    case 'P'.charCodeAt(0):
                        this.pause();
                        break;
                    case 37: // left
                        this.move(-1);
                        break;
                    case 39: // right
                        this.move(1);
                        break;
                    case 38: // up
                        this.rotate(1);
                        break;
                    case 40: // down
                        if (!e.repeat && this.state === 'running') {
                            this.speedup = true;
                            this.calcSpeed();
                            this.loop();
                        }
                        break;
                }
            },

            keyup(e) {
                switch (e.which) {
                    case 40: // down
                        if (!e.repeat) {
                            this.speedup = false;
                            this.calcSpeed();
                        }
                        break;
                }
            },

            move(x) {
                if (this.state !== 'running' || this.passThrough)
                    return;
                this.unsetFigureFromMap(this.board, this.current);
                this.current.pos[0] += x;
                let check = this.checkCollide(this.board, this.current);
                if (check === 0) {
                    this.setFigureToMap(this.board, this.current);
                    this.$refs.board.update();
                } else {
                    if (check !== 2 && this.current.figure.mode === 'explode')
                        return this.explode();
                    this.current.pos[0] -= x;
                    this.setFigureToMap(this.board, this.current);
                }
            },

            rotate(dir) {
                if (this.state !== 'running')
                    return;
                if (this.current.figure.mode === 'explode')
                    return this.explode();
                this.unsetFigureFromMap(this.board, this.current);
                let n = this.current.figure.pointsRotated.length;
                this.current.rotate = (n + (this.current.rotate + dir)) % n;
                let check = this.checkCollide(this.board, this.current);
                if (check === 0) {
                    this.setFigureToMap(this.board, this.current);
                    this.$refs.board.update();
                } else {
                    this.current.rotate = (n + (this.current.rotate - dir)) % n;
                    this.setFigureToMap(this.board, this.current);
                }
            },

            calcSpeed() {
                if (this.intervalId !== null)
                    clearInterval(this.intervalId);
                if (this.state === 'running') {
                    let delay = 50 + (1000 - 50) * (1 - (this.speedup ? 12 : this.speed) / 12);
                    this.intervalId = setInterval(this.loop.bind(this), delay);
                } else {
                    this.intervalId = null;
                }
            },

            loop() {
                if (!this.passThrough)
                    this.unsetFigureFromMap(this.board, this.current);
                this.current.pos[1] += 1;
                let check = this.checkCollide(this.board, this.current);
                if (check === 0) {
                    this.setFigureToMap(this.board, this.current);
                    if (this.current.figure.mode === 'ball') {
                        this.passThrough = false;
                        // this.discardBuilded(this.board);
                        this.setFigureToMap(this.board, this.current);
                    }
                    if (this.$refs.board) this.$refs.board.update();
                } else {
                    if (this.current.figure.mode === 'explode') return this.explode();
                    if (this.current.figure.mode === 'ball' && check === 1 && this.checkDownFree()) {
                        this.passThrough = true;
                        this.setFigureToMap(this.board, this.current);
                        this.$refs.board.update();
                        return;
                    }
                    this.current.pos[1] -= 1;
                    this.setFigureToMap(this.board, this.current);
                    this.discardBuilded(this.board);
                    if (this.$refs.board) this.$refs.board.update();

                    this.swapNew();

                    if (this.speedup) {
                        this.speedup = false;
                        this.calcSpeed();
                    }
                }
            },

            checkDownFree() {
                for (let y = this.current.pos[1] + 1; y < this.board.h; ++y)
                    if (this.board[this.current.pos[0] + y * this.board.w] === this.emptyColor)
                        return true;
                return false;
            },

            end() {
                this.highScore = Math.max(this.score, this.highScore);
                this.state = 'end';
                this.calcSpeed();
                if (this.score >= this.finScore[this.step - 1]) this.win = true;
                else this.win = false;
                this.start = false;
            },

            checkCollide(map, current) {
                for (let t of current.figure.pointsRotated[current.rotate]) {
                    let x = current.pos[0] + t[0];
                    let y = current.pos[1] + t[1];

                    if (x < 0 || x >= map.w || y < 0 || y >= map.h) {
                        if (y >= map.h)
                        current.color = 'final';
                        // this.setFigureToMap(this.board, this.current);
                        return 2;
                    }
                    if (map[x + y * map.w] !== this.emptyColor) {
                        current.color = 'final';
                        // this.setFigureToMap(this.board, this.current);
                        return 1;
                    }
                }
                return 0;
            },

            discardBuilded(map) {
                for (let y = map.h - 1; y >= 0; --y) {
                    let target = y;
                    while (target >= 0) {
                        let n = 0;
                        for (let x = 0; x < map.w; ++x)
                            if (map[x + target * map.w] !== this.emptyColor)
                                ++n;
                        if (n !== map.w)
                            break;
                        --target;
                        ++this.score;
                        document.querySelector('.board').style.backgroundPositionY = this.brickSize * this.score + 0.07 * (this.score+1) +'vh';
                    }
                    if (target !== y) {
                        let sh = y - target;
                        for (let j = y; j >= sh; --j) {
                            for (let x = 0; x < map.w; ++x)
                                if (map[x + (j - sh) * map.w] !== 'prev') map[x + j * map.w] = map[x + (j - sh) * map.w];
                        }
                    }
                }
            },

            newGame() {
                this.figures = this.figuresAll.filter(f => !f.disabled);
                console.log(this.figures[0]);
                if (!this.figures.length)
                    return alert('no figures selected');
                this.board.w = +this.board.w;
                this.board.h = +this.board.h;
                this.state = 'end';
                this.bucket = [];
                this.last = null;
                this.calcSpeed();
                this.initBoard();
                this.initPreview();

                this.highScore = Math.max(this.score, this.highScore);
                this.score = 0;
                this.state = 'running';
                this.current = this.genNew();
                this.setFigureToMap(this.board, this.current);
                this.next = this.genNew();
                this.setFigureToMap(this.preview, this.next, true);
                this.intervalId = null;
                this.calcSpeed();
                this.loop();

                if (this.$refs.preview) this.$refs.preview.update();
            },

            showSettings() {
                this.state = 'settings';
                this.calcSpeed();
            },

            explode() {
                for (let x = 0; x < 4; ++x) {
                    for (let y = 0; y < 4; ++y) {
                        let xe = x + this.current.pos[0];
                        let ye = y + this.current.pos[1];
                        if (xe >= 0 && xe < this.board.w && ye >= 0 && ye < this.board.h)
                            this.board[xe + ye * this.board.w] = this.emptyColor;
                    }
                }
                this.$refs.board.update();
                this.swapNew();
            },

        },

        created() {
            this.board.w = 10;
            this.board.h = 18;
            this.preview.w = 4;
            this.preview.h = 4;
            this.initFigures();
            // this.newGame();
        },

        mounted() {

        },
    }
</script>

